import React, { Component } from 'react';
import { Icon, Dimmer } from 'semantic-ui-react';
import { navigate } from 'gatsby';

export default class DimTile extends Component {
  constructor() {
    super();
    this.state = { active: false };
  }

  handleShow = () => this.setState({ active: true });
  handleHide = () => this.setState({ active: false });

  render() {
    const { active } = this.state;

    return (
      <Dimmer.Dimmable
        as="div"
        className={`vertical-center-container ${this.props.to ? 'dim-tile' : ''}`}
        style={{ backgroundColor: this.props.color, flex: '1 0 auto', margin: 5, boxShadow: '0 2px 8px 3px rgba(0, 0, 0, 0.3)' }}
        dimmed={active}
        onMouseEnter={this.handleShow}
        onMouseLeave={this.handleHide}
      >
        {this.props.asLink ?
          <Dimmer active={active} as='a' href={this.props.to}>
            <p style={{ fontSize: 16, textAlign: 'left', margin: 16 }}>
              {this.props.message}
            </p>
          </Dimmer> :
          <Dimmer active={active} onClick={() => navigate(this.props.to)}>
            <p style={{ fontSize: 16, textAlign: 'left', margin: 16 }}>
              {this.props.message}
            </p>
          </Dimmer>
        }
        <div className="vertical-center-container" style={{ width: 200 }}>
          <h1 className="tile-header">{this.props.name}</h1>
          <Icon inverted fitted name={this.props.icon} size='huge' />
        </div>
      </Dimmer.Dimmable>
    )
  }
}